:root {
  --color-primary: #27343d;
  --color-primary-2: #fff;
  --color-primary-3: #27343d2d;
  --color-primary-4: #27343d8f;
  --color-primary-5: #e2e5e4;
  --color-primary-7: #f7f7f7;
  --color-dark: #0f0f0f;
  --color-dark-2: #0f0f0f7a;
  --color-gray-dark: #656f76;
  --color-gray-dark-li: #6c6c6c;
  --color-gray: #c7c7c7;
  --color-gray-2: #d4d4d4;
  --color-gray-3: #bababa;
  --color-black: #000;
  --shadow-dark: 0 2rem 6rem #0000004d;
  --shadow-light: 0 2rem 5rem #0000000f;
  --line: 1px solid var(--color-grey-light-2);
  --radius: 20px;
  --radius-2: 10px;
  --shadow-black-1: #00000007;
  --shadow-black-2: #00000021;
  --color-dark-shadow: #0f0f0ff6;
  --shadow-white: #f5f5f515;
  --color-primary-shadow: #27343d2d;
  --color-primary-shadow-2: #27343d05;
  --svg-color: #fff;
  --svg-hover-color: red;
}

@keyframes FadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes FadeIn2 {
  0% {
    opacity: 0;
  }

  100% {
    opacity: .9;
  }
}

@keyframes zoomOut {
  0% {
    transform-origin: 80% 0;
    transform: translate(-50%, -50%)scale(1.7);
  }

  100% {
    transform-origin: 70% 0;
    transform: translate(-50%, -50%)scale(1.3);
  }
}

@keyframes zoomIn {
  0% {
    transform: scale(.8);
  }

  100% {
    transform: scale(1.89);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes backgroundComeFromRight {
  0% {
    background-position: 150% 0;
  }

  100% {
    background-position: 75% 0;
  }
}

@keyframes backgroundComeFromRight2 {
  0% {
    background-position: 150% 0;
  }

  100% {
    background-position: 92.5% 0;
  }
}

@keyframes backgroundComeFromzoom {
  0% {
    background-size: 300%;
  }

  100% {
    background-size: 22%;
  }
}

@keyframes fadeInBottom {
  from {
    opacity: 0;
    transform: translateY(100%);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-100%);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInTop {
  from {
    opacity: 0;
    transform: translateY(-100%);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes rainbow_animation {
  0%, 100% {
    background-position: 0 0;
  }

  50% {
    background-position: 100% 0;
  }
}

@keyframes moveGradient {
  0%, 100% {
    transform: translate(0)rotate(0);
  }

  25% {
    transform: translate(20px, -20px)rotate(45deg);
  }

  50% {
    transform: translate(-20px, 20px)rotate(90deg);
  }

  75% {
    transform: translate(20px, 20px)rotate(135deg);
  }
}

@keyframes moveGradientAlt {
  0%, 100% {
    transform: translate(0)rotate(0);
  }

  25% {
    transform: translate(-20px, 20px)rotate(-45deg);
  }

  50% {
    transform: translate(20px, -20px)rotate(-90deg);
  }

  75% {
    transform: translate(-20px, -20px)rotate(-135deg);
  }
}

@keyframes gradientAnimation1 {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

@keyframes gradientAnimation2 {
  0% {
    background-position: 50% 0;
  }

  50% {
    background-position: 50% 100%;
  }

  100% {
    background-position: 50% 0;
  }
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeOutUp {
  0% {
    opacity: 1;
    transform: translateY(0);
  }

  100% {
    opacity: 0;
    transform: translateY(-20px);
  }
}

@keyframes blink {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

*, :after, :before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  font-size: 62.5%;
}

@media only screen and (width <= 80.5em) {
  html {
    font-size: 56.25%;
  }
}

@media only screen and (width <= 56.25em) {
  html {
    font-size: 50%;
  }
}

@media only screen and (width >= 80.5em) {
  html {
    font-size: 50%;
  }
}

@media only screen and (width >= 114em) {
  html {
    font-size: 65%;
  }
}

body {
  box-sizing: border-box;
  background-color: #f8f9fb;
  overflow-x: hidden;
}

@media only screen and (width <= 80.5em) {
  body {
    margin: 0;
  }
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

::selection {
  background-color: var(--color-primary);
  color: var(--color-gray);
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: var(--color-gray);
}

::-webkit-scrollbar-thumb {
  background: var(--color-primary);
}

::-webkit-scrollbar-thumb:hover {
  background: var(--color-primary-3);
}

.shadow {
  pointer-events: none;
  opacity: 0;
  background: linear-gradient(#00000081, #0000);
  width: 100%;
  height: 100%;
  animation: 1.2s ease-in-out forwards FadeIn;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.shadow:after {
  content: "";
  background: linear-gradient(to top, #00000080, #0000);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.shadow:before {
  content: "";
  background: linear-gradient(to right, var(--color-primary), #0000);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

@media only screen and (width <= 47.75em) {
  .shadow {
    display: none;
  }
}

.spinner-container {
  z-index: 9999999;
  opacity: 1;
  visibility: visible;
  background-color: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: opacity 1s ease-in;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.spinner-container.hidden {
  opacity: 0;
  visibility: hidden;
}

.spinner-video {
  object-fit: cover;
  width: 12.5rem;
  position: relative;
}

.spinner-video:before {
  content: "";
  background-color: red;
  width: 100%;
  height: 100%;
}

#loading-bar-container {
  background-color: #fff;
  width: 10rem;
  height: 3px;
  position: absolute;
  top: 55%;
}

#loading-bar {
  background-color: var(--color-primary);
  border-radius: 50px;
  width: 0;
  height: 100%;
  transition: width .1s, background-color .1s;
}

.arr {
  font-family: Arial Unicode MS, Lucida Sans Unicode, sans-serif;
}

body {
  font-family: Poppins, sans-serif;
  font-weight: 400;
  line-height: 1.7;
}

.u-center-text {
  text-align: center !important;
}

.u-margin-bottom-small {
  margin-bottom: 1.5rem !important;
}

.u-margin-bottom-medium {
  margin-bottom: 4rem !important;
}

@media only screen and (width <= 56.25em) {
  .u-margin-bottom-medium {
    margin-bottom: 3rem !important;
  }
}

.u-margin-bottom-big {
  margin-bottom: 8rem !important;
}

@media only screen and (width <= 56.25em) {
