.btn {
  box-sizing: content-box;
  z-index: 12;
  border-radius: var(--radius);
  cursor: pointer;
  justify-content: center;
  align-self: center;
  align-items: center;
  width: 16rem;
  height: 4rem;
  transition: border .2s ease-in, color .2s ease-out, background-color .3s ease-out, transform .3s ease-in-out, box-shadow .4s;
  display: flex;
}

.btn__text, .btn:link, .btn:visited {
  color: currentColor;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 600;
}

@media only screen and (width <= 47.75em) {
  .btn__text, .btn:link, .btn:visited {
    font-size: 1rem;
    font-weight: 800;
  }
}

.btn:hover:after, .btn:active:after {
  opacity: 0;
}

.btn:active, .btn:focus {
  box-shadow: 0 .5rem 1rem var(--shadow-white);
  outline: none;
  transform: translateY(2px);
}

.btn-hover:hover, .btn-hover:active {
  border: 2px solid var(--color-primary);
  color: var(--color-primary-2);
  background-color: var(--color-primary);
}

.btn-dark {
  background-color: var(--color-dark-2);
  border: 2px solid #0000;
  color: var(--color-primary-2) !important;
}

.btn-dark:hover, .btn-dark:active {
  border: 2px solid var(--color-dark);
  background-color: var(--color-dark);
}

.btn--wide {
  width: 20rem !important;
}

.btn--white {
  border: 2px solid var(--color-primary-2);
  color: var(--color-primary);
  background-color: var(--color-primary-2);
}

@media only screen and (width <= 47.75em) {
  .btn--white {
    max-width: 13rem;
    padding: 0;
  }
}

.btn--white:hover, .btn--white:active {
  opacity: .8;
}

.btn--link {
  text-transform: uppercase;
  color: var(--color-primary-2);
  text-decoration: none;
}

.btn-darkin {
  border: 2px solid var(--color-primary);
  background-color: #0000;
  color: var(--color-primary) !important;
}

.btn-darkin:hover, .btn-darkin:active {
  border: 2px solid var(--color-primary);
  background-color: var(--color-primary);
  color: var(--color-primary-2) !important;
}

#btn-mobile {
  max-width: 10.4rem;
  padding: 0 .8rem;
}

#btn-mobile .btn__text {
  font-size: 1.1rem;
  font-weight: 700;
}

.btn-light {
  border: 2px solid var(--color-primary-2);
  background-color: #0000;
  transition: all .1s;
  color: var(--color-primary-2) !important;
}

.btn-light:hover, .btn-light:active {
  border: 2px solid var(--color-primary-2);
  background-color: var(--color-primary-2);
  color: var(--color-primary) !important;
}

.solutions-Section {
  background-color: #f7f7f7;
  height: 100vh;
  padding: 0 15rem;
  position: relative;
  top: 10rem;
}

.carousel-container {
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: visible;
}

.carousel {
  background-color: #f7f7f7;
  align-content: flex-start;
  align-items: center;
  gap: 2rem;
  width: 100%;
  height: 50vh;
  margin-top: 12.8rem;
  transition: all .5s;
  display: flex;
  overflow: visible;
}

.carousel-item {
  box-sizing: border-box;
  opacity: .99;
  background-color: #dbdbdb00;
  border-radius: 25px;
  width: 27.5rem;
  height: 35rem;
  transition: all .2s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2rem 5rem #00000021;
}

.carousel-item img {
  object-fit: cover;
  z-index: -1;
  width: 100%;
  height: 100%;
  position: absolute;
  overflow: hidden;
}

.carousel-item:after {
  content: "";
  z-index: -1;
  background: linear-gradient(0deg, #000 0%, #0000008d 4%);
  width: 100%;
  height: 100%;
  transition: all .3s;
  position: absolute;
  top: 0;
}

.carousel-item h3 {
  color: #d6d6d6c4;
  margin: 1.2rem 2.3rem;
  font-size: 2.4rem;
  transition: all .3s;
  position: absolute;
  bottom: 0;
}

.carousel-item p {
  opacity: 0;
  margin: 1.2rem 3.5rem;
  font-size: 1.6rem;
  font-weight: 500;
  transition: all .3s;
}

.carousel-item .sol-btn-go {
  background-color: var(--color-primary);
  color: var(--color-primary-7);
  opacity: 0;
  visibility: hidden;
  border-radius: 25px;
  padding: 1rem 1.6rem;
  font-size: 1.5rem;
  transition: all .3s 10ms;
  position: absolute;
  bottom: 2rem;
  left: 3rem;
}

.carousel-item.active {
  opacity: 1;
  z-index: 80;
  width: 52.5rem;
  height: 50rem;
}

.carousel-item.active p {
  opacity: 1;
  color: #ffffffeb;
}

.carousel-item.active h3 {
  color: #ffffffeb;
  font-size: 4rem;
  position: relative;
}

.carousel-item.active:after {
  background: linear-gradient(0deg, #000 0%, #000000d4 4%);
}

.carousel-item.active .sol-btn-go {
  color: #000000e1;
  opacity: 1;
  visibility: visible;
  background-color: #fffffff9;
}

.carousel-item.active .sol-btn-go:hover {
  opacity: .8;
}

.controls {
  display: flex;
  position: absolute;
  top: 60%;
  right: 40px;
  transform: translateY(-50%);
}

.controls button {
  cursor: pointer;
  background-color: #007bff00;
  border: 1px solid #00000065;
  border-radius: 100px;
  width: 42.5px;
  height: 42.5px;
  margin: 5px 8px;
  font-size: 2rem;
  transition: all .2s;
}

.controls button:hover {
  background-color: #ffffffe9;
  border: 1px solid #000000cd;
}

