.work-process {
  backdrop-filter: blur(25px);
  color: #343434;
  text-align: center;
  opacity: 1;
  z-index: 100;
  border-radius: var(--radius-2);
  background-color: #fff;
  flex-direction: column;
  align-content: center;
  padding: 20rem 0 25rem;
  transition: background-color 1s, color .5s;
  display: flex;
  overflow: hidden;
  transform: scale(1);
