@keyframes FadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
@keyframes FadeIn2 {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.9;
  }
}
@keyframes zoomOut {
  0% {
    transform-origin: 80% 0%;
    transform: translate(-50%, -50%) scale(1.7); /* Adjust the scale as needed */
  }
  100% {
    transform-origin: 70% 0%;
    transform: translate(-50%, -50%) scale(1.3);
  }
}

@keyframes zoomIn {
  0% {
    transform:scale(0.8); /* Adjust the scale as needed */
  }
  100% {
    transform: scale(1.89);
  }
}


@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}



@keyframes backgroundComeFromRight {
  0% {
    background-position: 150% 0; 
  }
  100% {
    background-position: 75% 0; 
  }
}
@keyframes backgroundComeFromRight2 {
  0% {
    background-position: 150% 0; 
  }
  100% {
    background-position: 92.5% 0; 
  }
}

@keyframes backgroundComeFromzoom {
  0% {
    background-size: 300%;
  }
  100% {
    background-size: 22%;
  }
}






@keyframes fadeInBottom {
  from {
    opacity: 0;
    transform: translateY(100%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-100%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInTop {
  from {
    opacity: 0;
    transform: translateY(-100%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}



@keyframes rainbow_animation {
	0%,
	100% {
		background-position: 0 0;
	}

	50% {
		background-position: 100% 0;
	}
}




@keyframes moveGradient {
  0%, 100% {
    transform: translate(0, 0) rotate(0deg);
  }
  25% {
    transform: translate(20px, -20px) rotate(45deg);
  }
  50% {
    transform: translate(-20px, 20px) rotate(90deg);
  }
  75% {
    transform: translate(20px, 20px) rotate(135deg);
  }
}

@keyframes moveGradientAlt {
  0%, 100% {
    transform: translate(0, 0) rotate(0deg);
  }
  25% {
    transform: translate(-20px, 20px) rotate(-45deg);
  }
  50% {
    transform: translate(20px, -20px) rotate(-90deg);
  }
  75% {
    transform: translate(-20px, -20px) rotate(-135deg);
  }
}
@keyframes gradientAnimation1 {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

@keyframes gradientAnimation2 {
  0% { background-position: 50% 0%; }
  50% { background-position: 50% 100%; }
  100% { background-position: 50% 0%; }
}


@keyframes fadeInUp {
  0% {
      opacity: 0;
      transform: translateY(20px);
  }
  100% {
      opacity: 1;
      transform: translateY(0);
  }
}

@keyframes fadeOutUp {
  0% {
      opacity: 1;
      transform: translateY(0);
  }
  100% {
      opacity: 0;
      transform: translateY(-20px);
  }
}

@keyframes blink {
  0% {
      opacity: 1;
  }
  50% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}