// Responsive mixin
@mixin respond($breakpoint) {
  @if $breakpoint == phone {
    @media only screen and (max-width: 47.75em) {
      @content;
    } // 600px
  }
  @if $breakpoint == smallphone {
    @media only screen and (max-width: 22.89em) {
      @content;
    } // 600px
  }
  @if $breakpoint == midphone {
    @media only screen and (max-width: 30em) {
      @content;
    } // 600px
  }
  @if $breakpoint == midphone2 {
    @media only screen and (max-width: 37em) {
      @content;
    } // 600px
  }


  @else if $breakpoint == tab-port {
    @media only screen and (max-width: 56.25em) {
      @content;
    } // 900px
  }
  @else if $breakpoint == tab-land {
    @media only screen and (max-width: 80.5em) {
      @content;
    } // 1200px
  }
  @else if $breakpoint == mid-desktop {
    @media only screen and (min-width: 80.5em) {
      @content;
    } // 1800px
  }
  @else if $breakpoint == mid-desktop-2 {
    @media only screen and (max-width: 90.5em) {
      @content;
    } // 1800px
  }
  @else if $breakpoint == big-desktop {
    @media only screen and (min-width: 114em) {
      @content;
    } // 1800px
  }


  @else if $breakpoint == AllexPhone {
    @media only screen and (min-width: 56.25em) {
      @content;
    } // 1800px
  }
}





