.center {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  justify-items: center;

}
.form-page {
    display: grid;
    grid-template-columns: 50% 50%;
    min-height: 105vh; /* Ensure the section is at least the height of the viewport */
    &__text {
      background-color: #e8e8e8;
      background: linear-gradient(120deg, #000000, #360000);
      background-size: 400% 400%;
      animation: gradientAnimation1 15s ease infinite;
      backdrop-filter: blur(10px); // Add blur effect
      padding:0 5rem;
      margin: 0 auto;
      width: 100%;
      height: 100%;
      position: relative;
  
      align-items: flex-start;



    }
    
}

.form-page__text {
  background-color: transparent;
  flex-direction: column;
  align-items: flex-start;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  position: relative;
}

.form-page__text__title {
  opacity: 0;
  animation: fadeIn 0.5s forwards;
  margin-bottom: 10px; /* Ensure consistent spacing */
}

.q {
  position: relative;
  margin-top: -17.5rem;
  width: 100%;
}

.q-title {
  font-size: 3rem;
  font-weight: 700;
  opacity: 0;
  margin-bottom: 1rem;
  color: #d4d4d4;
  transition: opacity 0.5s; /* Smooth transition */
}

.q-content-wrapper {
  opacity: 0;
  position: absolute;
  max-width: 90%;
  transition: opacity 0.5s; /* Smooth transition */
}

.q-content {
  font-size: 2.5rem;
  font-weight: 400;
  color: #d4d4d4;
}

.cursor {
  width: 25px;
  height: 25px;
  background-color: rgba(247, 247, 247, 0.629);
  border-radius: 50%;
  animation: blink 1.5s infinite;
  position: absolute;
  bottom: 0.8rem;
  vertical-align: middle; /* Align vertically with text */
}



////
.form-page-container {
    background: linear-gradient(120deg, #F7F7F7, #E2E5E4);
    background-size: 400% 400%;
    animation: gradientAnimation1 15s ease infinite;
    backdrop-filter: blur(10px); // Add blur effect
    text-align: center;
    overflow: hidden;
    width: 100%;
    height: 100%;
    opacity: 0;
    transform: scale(0.8);
    transition: opacity 0.5s, transform 0.5s;

    img {
      width: 4rem;
      margin-bottom: -2rem;
    }
    h2 {
        font-size: 2.3rem;
        color: #707070;
    }

    &__personal-img {
      width: 3rem !important;
    }
    form {
      margin-top: -5rem;
    }
}
.form-container {
    height: 100%; /* Adjust height as needed */
    width: 100%;
    align-self: center;
  }

  .progress-bar {
    display: flex;
    justify-content: space-between;
    align-content: center;
  }
  
  .progress-bar__step {
    text-align: center;
    height: 2rem;
    width: 2rem;
    border-radius: 100px;
    background-color: #d4d4d4;
    opacity: 0.8;
  }
  .progress-bar__line {
    width: 35px;
    height: 2px;
    align-self: center;
    margin-top: 3px;
    background-color: #c8c8c8;
  }

  .progress-bar__step--active {
    background-color: #000000;
    opacity: 1;
  }
  

  
  .form-step {
    display: none;

  }
  
  .form-step--active {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2.5rem;
    margin: 0 auto;
    min-height: 30rem;
    min-width: 60rem;
  }
  
  .buttons {
    display: flex;
    justify-content: center;
    gap: 50%;
  }

  
  .form-btn {
    padding: 15px 25px;
    border: 0;
    border-radius: var(--radius);
    background-color: transparent;
    color: rgb(0, 0, 0);
    transition: all .1s;
    box-shadow: 0 1rem 5rem #00000013;
    cursor: pointer;
  }
  
  .form-btn.back {
    justify-self: flex-start;
  }
  
  .form-btn:hover {
    background-color: #00000011;
  }






.form__step-title {
    font-size: 3rem;
    color: #F7F7F7;
    margin: 5rem 0 2.5rem 0;
}


.selection-button {
  padding: 1rem 2rem;
  border-radius: 25px;
  background-color: #cacacaba;
  color: #000000;
  border: none;
  margin:  0 1rem;
  cursor: pointer;
  &:hover {
    background-color: #000;
    color: #fff;
  }
}


select {
    width: 20%;
    padding: 0 25px;
    color: #F7F7F7;
    background-image: linear-gradient(to right, #434343 0%, rgb(52, 52, 52) 100%);
    border: none;
    font-size: 1.7rem;
    font-weight: 500;
    text-align: center;
    border-radius: var(--radius-2);
    height: 4rem;
}



input.error, select.error {
    border-color: red;
  }
  



/////MAP///////
  .mapAnd_info {
    position: relative;
    margin-top: 5rem;
    max-height: 50rem; /* Adjust height as needed */
    max-width: 70rem;
    
    &__address {
      position: absolute;
      color: #969696;
      font-size: 1.5rem;
      font-weight: 400;
      bottom: 0;
      margin: 0 3rem 1.9rem 3rem;
      z-index: 2;
      pointer-events: none; /* Ensure it doesn't intercept clicks */
      opacity: 1;
      transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;  
      span {
        bottom: 25px;
        font-size: 1rem;
        font-weight: 600;
        position: absolute;
      }
    }
    
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 80%;
      background: linear-gradient(360deg, rgba(0, 0, 0, 0.805) 21%, rgba(225,232,235,0) 100%);
      z-index: 1;
      pointer-events: none; /* Ensure it doesn't intercept clicks */
      opacity: 1;
      transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
  
    }

    &__map {
      height: 100%; /* Adjust height as needed */
      width: 100%;
      border-radius: var(--radius-2);
      z-index: 0; /* Ensure it is below the other elements */
      position: relative; 
      font-size: 5rem;
    
    }

    &:hover {
      &::after {
        opacity: 0;
        transform: translateY(20px); /* Adjust as needed for animation effect */
      }
        .mapAnd_info__address {
        opacity: 0;
        transform: translateY(20px); /* Adjust as needed for animation effect */
      }
    }
  }
  


