.solutions-Section {
    height: 100vh ;
    background-color: #F7F7F7;
    padding: 0rem 15rem;
    position: relative;
    top: 10rem;
}

.carousel-container {
    width: 100%;
    height: 100vh ;
    position: relative;
    overflow:visible;
  }
  
  .carousel {
    display: flex;
    background-color: #F7F7F7;
    align-content: flex-start;
    align-items: center;
    height: 50vh;
    gap: 2rem;
    margin-top: 12.8rem;
    width: 100%;
    overflow:visible;
    transition: all 0.5s ease;

  }
  
  .carousel-item {
    box-sizing: border-box;
    opacity: 0.99;
    box-shadow: 0 2rem 5rem #00000021;
    background-color: #dbdbdb00;
    width: 27.5rem;
    height: 35rem;
    border-radius: 25px;
    overflow: hidden;
    position: relative;
    transition: all .2s;
    img {
        width:100%;
        overflow: hidden;

        height:100%;
        object-fit: cover;
        position: absolute;
        z-index: -1;
      }

    &::after {
        content: '';
        position: absolute;
        z-index: -1;
        top: 0;
        width: 100%;
        height: 100%;
        background: rgb(0,0,0);
        background: linear-gradient(0deg, rgb(0, 0, 0) 0%, rgba(0, 0, 0, 0.551) 4%);    
        transition: all .3s;
     }

     h3 {
        font-size: 2.4rem;
        color: #d6d6d6c4;
        margin: 1.2rem 2.3rem;
        position: absolute;
        bottom: 0;
        transition: all .3s;
     }
     p{
        opacity: 0;
        font-size: 1.6rem;
        margin: 1.2rem 3.5rem;
        font-weight: 500;
        transition: all .3s ;
      }
     .sol-btn-go {
        padding: 1rem 1.6rem;
        font-size: 1.5rem;
        border-radius: 25px;
        background-color: var(--color-primary);
        color: var(--color-primary-7);
        position: absolute;
        bottom: 2rem;
        left: 3rem;
        opacity: 0;
        visibility: hidden;
        transition: all .3s .01s;
        
     }
  }
  

  

  
  .carousel-item.active {
    width: 52.5rem;
    height: 50rem;
    opacity: 1;
    z-index: 80;
    p {
        opacity: 1;
        color: #ffffffeb;
    }
    h3 {
        font-size: 4rem;
        position: relative;
        color: #ffffffeb;
     }

     &::after {
        background: rgb(0,0,0);
        background: linear-gradient(0deg, rgb(0, 0, 0) 0%, rgba(0, 0, 0, 0.831) 4%);    
    }

    .sol-btn-go {
        background-color: rgba(255, 255, 255, 0.975);
        color: #000000e1;
        opacity: 1;
        visibility: visible;
        &:hover {
            opacity: 0.8;  
        }

     }
  }
  
  .controls {
    position: absolute;
    top: 60%;
    right: 40px;
    transform: translateY(-50%);
    display: flex;

  }
  
  .controls button {
    margin: 5px 8px;
    width: 42.5px;
    height: 42.5px;
    font-size: 2rem;
    border-radius: 100px;
    background-color: #007bff00;
    border: 1px solid #00000065;
    cursor: pointer;
    transition: all .2s;
  }
  
  .controls button:hover {
    background-color: #ffffffe9;
    border: 1px solid #000000cd;

  }
